<template>
    <div>
      <header class="mui-bar mui-bar-nav">
        <a class="mui-action-back mui-icon mui-icon-left-nav mui-pull-left" @click="goback"></a>
        <h1 class="mui-title">{{title}}</h1>
      </header>
    </div>
</template>

<script>
  export default {
    name: "navBar",
    data(){
      return {}
    },
    props:['title'],
    methods:{
      goback(){
        this.$router.go(-1)
      }
    }
  }
</script>

<style scoped>
.mui-bar.mui-bar-nav{
  position: relative;
  z-index: 99;
}
</style>



